.services-description {
    
    width: 100%; 
    display: flex; flex-direction: column;
    margin-bottom: 3rem;
}
.services-description .title {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
    margin-top: 1rem;
    text-align: center;
}

.services-description .title h2 {
    color: white;
    font-size: 28px; font-weight: 700;
    
    letter-spacing: 1px;
    line-height: 0.9;
    padding: 1rem; margin: 0;
}

.services-description .services-list {
    display: flex; flex-direction: column;
    padding: 0.5rem;
}
.services-description .services-list  .service {
    display: flex;
    align-items: center; 
    justify-content: center;
    padding: 1rem; margin-bottom: 0.5rem;
    border: 1px solid rgba(253, 253, 253, 0.24);
    border-radius: 0.5rem;
}
.services-description .services-list  .service .icon {
    font-size: 2rem;
}
.services-description .services-list  .service h3 {
    color: white;
    font-size: 22px; font-weight: 200;
    letter-spacing: 1px;
    line-height: 1;
    margin: 0; margin-left: 1rem;
    text-align: start;

}

@media only screen and (min-width: 769px) and (max-width: 1024px) {
    
    .services-description .services-list {
        padding: 1rem;
    }
    .services-description .services-list .service {
        width: 100%;
        padding:2rem;
    }
    .services-description .services-list .service h3 {
        font-size: 32px;
        
        
    }
    .services-description .services-list .service .icon {
        font-size: 3rem;
    }
}